.header-base {
  position: fixed;
  z-index: 10000;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 70px;
  background-color: #1C1C1C;
  border-bottom: thin solid #000000; 
  border-width: 2px;
}

.header-animation {
	transition: 1s ease;
	background-color: white;
}

.header-animation-back {
	transition: 1s ease;
	background-color: #1C1C1C;
}

